@font-face {
  font-weight: 500;
  font-family: "Poppins";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/poppins-medium.woff2") format("woff2"), url("../fonts/poppins-medium.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "Poppins";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/poppins-bold.woff2") format("woff2"), url("../fonts/poppins-bold.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/cormorant-garamond-bold.woff2") format("woff2"), url("../fonts/cormorant-garamond-bold.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "Cormorant Garamond";
  font-style: italic;
  font-display: swap;
  src: url("../fonts/cormorant-garamond-bold-italic.woff2") format("woff2"), url("../fonts/cormorant-garamond-bold-italic.woff") format("woff"); }

html {
  scroll-behavior: smooth;
  height: 100%; }

body {
  position: relative;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 21px;
  font-weight: 500;
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  color: #1d1613;
  background-color: #ffffff;
  height: 100%; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  background-position: 0 0; }

.main-page__wrapper {
  margin-top: 30px;
  padding: 0 40px;
  max-width: 1100px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.main-page__wrapper h1 {
  margin: 0;
  font-size: 42px;
  line-height: 63px;
  text-align: center; }

.main-page__add-task {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 100%;
  margin-top: 30px; }

.main-page__add-task input {
  font-size: 20px;
  line-height: 30px;
  -webkit-box-flex: 2;
      -ms-flex-positive: 2;
          flex-grow: 2;
  border: 2px solid #000000;
  cursor: pointer;
  border-radius: 5px; }
  .main-page__add-task input:hover, .main-page__add-task input:focus {
    border: 2px solid #903535; }

.main-page__new-task-btn,
.main-page__clear-task-btn {
  width: 40px;
  margin-left: 20px;
  padding: 0;
  height: auto;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer; }
  .main-page__new-task-btn:hover svg,
  .main-page__new-task-btn:focus svg,
  .main-page__clear-task-btn:hover svg,
  .main-page__clear-task-btn:focus svg {
    stroke: #903535; }

.main-page__list {
  margin: 0;
  padding: 0;
  margin-top: 40px; }

.list {
  list-style: none; }

.list li {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 10px;
  padding-right: 175px; }
  .list li:hover {
    color: #903535; }

.list li:first-child {
  margin-top: 0; }

li:hover .list__button--opened {
  display: block; }

li:hover input:not(:checked) + label::before {
  background-image: url("../../img/hover-circle.svg"); }

li input:focus:not(:checked) + label::before {
  background-image: url("../../img/hover-circle.svg"); }

li input:focus:not(:checked) ~ label {
  color: #903535; }

li:hover input:checked + label::after,
li input:checked + label:focus::after {
  background-image: url("../../img/check-circle-red.svg"); }

.list__check-wrapper span {
  margin-right: 10px;
  font-size: 20px;
  line-height: 38px; }

.list__check-wrapper label {
  position: relative;
  display: block;
  width: 100%;
  word-wrap: break-word;
  height: auto;
  padding-left: 55px;
  font-size: 20px;
  line-height: 35px;
  cursor: pointer;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

li .list__check-wrapper {
  position: relative;
  margin: 0;
  width: 100%;
  height: auto; }
  li .list__check-wrapper::after {
    content: "";
    position: absolute;
    bottom: -5px;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: black;
    opacity: 0.2; }

.list__check-wrapper--edit {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.list__check-wrapper--edit label {
  width: auto; }

.list__check-wrapper--edit:hover input,
li:hover input {
  color: #903535; }

.list__input-edit-wrapper {
  width: 100%; }

.list__input-edit-wrapper input {
  border: none;
  font-size: 20px;
  line-height: 35px;
  height: 100%;
  width: 100%;
  padding: 0;
  cursor: pointer;
  outline: none; }
  .list__input-edit-wrapper input:hover {
    color: #903535; }

li:last-child .list__check-wrapper::after {
  display: none; }

.list__check-wrapper input:not(:checked) + label::before {
  content: "";
  position: absolute;
  width: 40px;
  height: 40px;
  top: 0;
  left: 0;
  background-image: url("../../img/circle.svg");
  background-repeat: no-repeat;
  background-size: 40px 40px;
  background-position: center center;
  background-color: transparent;
  cursor: pointer; }

.list__check-wrapper--important input:not(:checked) + label::before {
  background-image: url("../../img/alert-circle.svg"); }

.list__check-wrapper:hover input:not(:checked) + label::before {
  background-image: url("../../img/hover-circle.svg"); }

.list__check-wrapper--important:hover input:not(:checked) + label::before {
  background-image: url("../../img/alert-circle.svg"); }

li:hover .list__check-wrapper--important input:not(:checked) + label::before {
  background-image: url("../../img/alert-circle.svg"); }

.list__check-wrapper input:checked + label {
  text-decoration: line-through; }
  .list__check-wrapper input:checked + label::after {
    content: "";
    position: absolute;
    width: 40px;
    height: 40px;
    top: 0;
    left: 0;
    opacity: 0.5;
    background-image: url("../../img/check-circle.svg");
    background-repeat: no-repeat;
    background-size: 40px 40px;
    background-position: center center; }

.list__check-wrapper:hover input:checked + label::after {
  background-image: url("../../img/check-circle-red.svg"); }

.list__check-wrapper input:checked + label {
  opacity: 0.5; }

.list__button-wrapper {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%; }

.list__button-wrapper--closed {
  display: none; }

.list .list__button {
  position: relative;
  margin-left: 20px;
  min-width: 40px;
  height: 40px;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
  background-repeat: no-repeat;
  background-size: 32px 32px;
  background-position: center center;
  border-radius: 50%;
  outline: none; }
  .list .list__button--edit {
    background-image: url("../../img/edit.svg"); }
  .list .list__button--trash {
    background-image: url("../../img/trash.svg"); }
  .list .list__button--important {
    background-size: 35px 35px;
    background-image: url("../../img/alert-circle.svg"); }
  .list .list__button:hover, .list .list__button:focus {
    opacity: 0.5; }

@media (max-width: 1023px) {
  .list__check-wrapper label {
    font-size: 18px;
    line-height: 33px; } }
